body,
html {
    .resetmp;
    position: relative;
    overflow-x: hidden;

    main {
        padding-top: 90px;

        .simplebar-scrollbar {
            width: 5px;

            &::before {
                background: @primary-gradient;
            }
        }
    }

    footer {
        height: 100px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
        margin-top: 20px;

        p {
            .resetmp;
            text-align: center;
            font-family: "Barlow-Medium";
            font-size: 23px;
            color: @light-font-color;
            text-transform: uppercase;
        }
    }
}