div.counterNumbers {
    .g-pd-y;
    background: url("../images/recordBG.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 -30px;
    position: relative;

    &::before {
        content: "";
        width: 100%;
        height: 100%;
        .bg-gradient;
        opacity: .9;
        position: absolute;
        top: 0;
        left: 0;
    }

    .counter-wrap {
        text-align: center;
        font-family: "Barlow-Medium";
        color: @light-font-color;
        padding: 50px 0;

        h2 {
            display: inline-block;
            text-transform: uppercase;
            font-size: 50px;
            .resetmp;
        }

        span {
            display: inline;
            font-size: 50px;
            line-height: 0;
            .resetmp;
        }

        p {
            text-transform: capitalize;
            font-size: 23px;
            letter-spacing: 0;
        }
    }
}