section.aboutus {
    .g-pd-y;
    padding-bottom: 60px;

    .left-absection {

        .detail-text {
            height: 320px;
        }

        button.moreAbout {
            .myButton;
            padding: 10px 30px;
            font-size: 14px;
        }

        @media (min-width: 992px) {
            button.moreAbout {
                padding: 18px 50px;
                font-size: 18px;
            }
        }

        @media only screen and (min-width: 480px) and (max-width: 991px) and (orientation: portrait) {
            .detail-text {
                height: 250px;
            }
        }

        @media only screen and (min-width: 480px) and (max-width: 991px) and (orientation : landscape) {
            .detail-text {
                height: 280px;
            }
        }

        @media only screen and (min-width: 1500px) and (orientation : landscape) {
            .detail-text {
                height: 320px;
            }
        }

        @media only screen and (min-width: 1600px) and (orientation : landscape) {
            .detail-text {
                height: 370px;
            }
        }

        @media only screen and (min-width: 1920px) and (orientation : landscape) {
            .detail-text {
                height: 380px;
            }
        }

        @media only screen and (min-width: 2560px) and (orientation : landscape) {
            .detail-text {

                p {
                    line-height: 25px;
                }
            }
        }
    }

    .right-absection {
        position: relative;
        text-align: center;
        margin-top: 40px;

        .img1 {
            width: 83%;
            position: relative;
            filter: drop-shadow(3px 2px 1px rgba(0, 0, 0, .3));
            top: 0;
            left: 0;

            @media (min-width: 992px) {
                position: absolute;
                transform: translateX(-50%);
                left: 50%;
            }
        }

        .img2 {
            width: 83%;
            position: absolute;
            opacity: .4;
            top: 0;
            left: 70px;
            .horizontal;
        }

        .img3 {
            width: 75%;
            position: absolute;
            top: 0;
            left: 65px;
        }


        @media (min-width: 992px) {
            margin-top: unset;

            .img1 {
                top: 0;
                left: 0;
                transform: translateX(0);
            }

            .img2 {
                top: 0;
                left: 60px;
            }

            .img3 {
                top: 0;
                left: 45px;
            }
        }
    }

    .horizontal {
        animation: horizontal_4346 3s ease infinite;
        transform-origin: 50% 50%;
    }

    @keyframes horizontal_4346 {
        0% {
            transform: translate(0, 0)
        }

        6.97675% {
            transform: translate(3px, 0)
        }

        13.95349% {
            transform: translate(0, 0)
        }

        20.93023% {
            transform: translate(3px, 0)
        }

        27.90698% {
            transform: translate(0, 0)
        }

        34.88372% {
            transform: translate(3px, 0)
        }

        41.86047% {
            transform: translate(0, 0)
        }

        100% {
            transform: translate(0, 0)
        }
    }
}