.resetmp {
    margin: 0;
    padding: 0;
}

.bg-gradient {
    background: rgb(203, 45, 62);
    background: -moz-linear-gradient(0deg, rgba(203, 45, 62, 1) 0%, rgba(239, 71, 58, 1) 100%);
    background: -webkit-linear-gradient(0deg, rgba(203, 45, 62, 1) 0%, rgba(239, 71, 58, 1) 100%);
    background: linear-gradient(0deg, rgba(203, 45, 62, 1) 0%, rgba(239, 71, 58, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#cb2d3e", endColorstr="#ef473a", GradientType=1);
}

.myButton {
    .bg-gradient;
    font-family: "Barlow-Medium";
    text-transform: uppercase;
    letter-spacing: .5px;
    font-size: 18px;
    color: @light-font-color;
    border: 2px solid @light-font-color;
    -webkit-box-shadow: 10px 10px 10px -2px rgba(@primary-color-solid, 0.6);
    -moz-box-shadow: 10px 10px 10px -2px rgba(@primary-color-solid, 0.6);
    box-shadow: 10px 10px 10px -2px rgba(@primary-color-solid, 0.6);
    transition: all .5s;

    &:hover {
        box-shadow: -1px -1px 2px 0px rgba(black, 0.6);
        border: 2px solid lightgrey;
    }
}

.g-pd-y {
    padding-top: 40px;
    padding-bottom: 40px;
}