section.heroSlider {
    // border-bottom: 6px solid @primary-color-solid;
    position: relative;

    .hero-wrap {
        position: relative;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center 100%;
        height: 60vh;

        @media screen and (orientation: landscape) {
            height: 100vh;
        }

        @media (min-width: 992px) {
            height: 120vh;
        }

        &::before {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(@primary-color-solid, .12);
            position: absolute;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .text-wrap {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            color: @light-font-color;
            text-align: center;
            width: 90vw;

            @media (min-width: 992px) {
                width: auto;
            }

            h1 {
                font-family: "Barlow-ExtraBoldItalic";
                .font-size(90px);
                text-transform: uppercase;
                text-shadow: 3px 3px 4px rgba(0, 0, 0, 0.8);
            }
        }
    }

    .left-slider-btn {
        height: 100px;
        width: 50px;
        background-color: @primary-color-solid;
        clip-path: polygon(0% 0%, 80% 20%, 100% 50%, 80% 80%, 0% 100%);
        z-index: 2;
        cursor: pointer;
        position: absolute;
        left: -20px;
        top: 45% !important;
        transform: translateY(-50%);

        @supports (-ms-ime-align: auto) {
            height: 100px !important;
            border-top-right-radius: 50% !important;
            border-bottom-right-radius: 50% !important;
        }

        &::before {
            content: "";
            display: block;
            height: 97%;
            width: 95%;
            clip-path: polygon(0% 0%, 80% 20%, 100% 50%, 80% 80%, 0% 100%);
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 0;

            @supports (-ms-ime-align: auto) {
                border-top-right-radius: 50% !important;
                border-bottom-right-radius: 50% !important;
            }
        }

        &::after {
            content: url("../images/heroArow.png");
            position: absolute;
            top: 50%;
            filter: drop-shadow(-2px -2px 1px black);
            right: -5px;
            transform: translateY(-50%) scale(.4) rotate(180deg);
        }
    }

    .right-slider-btn {
        height: 100px;
        width: 50px;
        background-color: @primary-color-solid;
        clip-path: polygon(0% 0%, 80% 20%, 100% 50%, 80% 80%, 0% 100%);
        // clip-path: polygon(0% 0%, 40% 15%, 60% 50%, 40% 85%, 0% 100%);
        z-index: 3;
        cursor: pointer;
        position: absolute;
        top: 45% !important;
        right: -20px;
        transform: translateY(-50%) rotate(180deg);

        @supports (-ms-ime-align: auto) {
            height: 100px !important;
            border-top-right-radius: 50% !important;
            border-bottom-right-radius: 50% !important;
        }

        &::before {
            content: "";
            display: block;
            height: 97%;
            width: 95%;
            clip-path: polygon(0% 0%, 80% 20%, 100% 50%, 80% 80%, 0% 100%);
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 0;

            @supports (-ms-ime-align: auto) {
                border-top-right-radius: 50% !important;
                border-bottom-right-radius: 50% !important;
            }
        }

        &::after {
            content: url("../images/heroArow.png");
            position: absolute;
            top: 50%;
            filter: drop-shadow(2px 2px 1px black);
            right: -5px;
            transform: translateY(-50%) scale(.4) rotate(180deg);
        }
    }

    .slide-progress {
        width: 0%;
        max-width: 100%;
        height: 6px;
        background: @primary-color-solid;
    }



    @media (min-width: 992px) {
        .left-slider-btn {
            height: 200px;
            width: 100px;
            top: 50%;
            left: -30px;

            &::after {
                right: 20px;
                transform: translateY(-50%) scale(.8) rotate(180deg);
            }
        }

        .right-slider-btn {
            height: 200px;
            width: 100px;
            top: 50%;
            right: -30px;

            &::after {
                right: 20px;
                transform: translateY(-50%) scale(.8) rotate(180deg);
            }
        }
    }

    @media only screen and (orientation: landscape) {
        .left-slider-btn {
            top: 50%;
        }

        .right-slider-btn {
            top: 50%;
        }
    }
}