section.contact {
    .g-pd-y;
    background: url("../images/contactBG1.png"), url("../images/contactBG2.png");
    background-position: -35% 60%, 135% 60%;
    background-repeat: no-repeat;
    background-size: 530px;

    .outter {
        min-height: 585px;

        .side-contact-bar {
            .g-pd-y;
            .bg-gradient;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            border: 2px solid #fff;
            box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, .8);
            position: relative;
            z-index: 2;

            .contact-item-wrap {
                text-align: center;
                margin-bottom: 25px;

                &:last-of-type {
                    margin-bottom: 0;
                }

                img {
                    width: 50px;
                    filter: drop-shadow(1px 1px 1px black);
                }

                a {
                    display: block;
                    text-decoration: none;
                    color: @light-font-color;
                    font-family: "Barlow-MediumItalic";
                    font-size: 20px;
                    letter-spacing: .5px;
                }
            }
        }

        form {
            height: 100%;
            position: relative;
            z-index: 1;
            border: 2px solid #fff;
            border-left: 0;
            box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, .8);
            // padding: 0 80px;
            background-color: @secondary-color;
            .g-pd-y;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .input-addon {
                width: 70%;
                position: relative;
                margin-bottom: 40px;

                &:last-of-type {
                    margin-bottom: 0;
                }

                input {
                    .resetmp;
                    width: 100%;
                    display: block;
                    margin: 0 auto;
                    font-family: "Barlow-Light" !important;
                    font-size: 13px;
                    letter-spacing: .5px;
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid @light-font-color;
                    color: @light-font-color;
                    padding: 10px 0;


                }

                img {
                    width: 15px;
                    position: absolute;
                    top: 10px;
                    right: 0;
                }

            }

            button {
                .myButton;
                margin: 0 auto;
                margin-top: 80px;
                font-size: 16px;
                width: 70%;
                padding: 15px 0;
                box-shadow: 10px 10px 10px -2px rgba(black, 0.6);
                transition: all .5s;

                &:hover {
                    box-shadow: -2px -2px 2px 0px rgba(black, 0.6);
                    border: 2px solid lightgrey;
                }
            }
        }
    }
}