section.services {
    .g-pd-y;

    .services-wrap {
        background-image: url("../images/servicesBG.png");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        background-repeat: repeat-y;

        @media (min-width: 992px) {
            background-repeat: no-repeat;
        }

        .card-wrap {
            color: @light-font-color;
            background: @secondary-color;
            border: 2px solid @light-font-color;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            padding: 35px 10px 20px 10px;
            box-shadow: 3px 2px 6px 1px rgba(@dark-font-color, .5);
            margin-bottom: 20px;

            .img-wrap {
                height: 100px;
                width: 100px;
                border-radius: 50%;
                border: 2px solid @light-font-color;
                display: flex;
                justify-content: center;
                align-items: center;
                .bg-gradient;
                box-shadow: 1px 2px 10px 0px rgba(11, 11, 11, 1);

                img {
                    width: 50px;
                    filter: drop-shadow(1px 1px 1px black);
                }
            }

            h3 {
                position: relative;
                margin-top: 10px;

                &::after {
                    content: "";
                    display: block;
                    width: 55px;
                    height: 3px;
                    .bg-gradient;
                    position: absolute;
                    bottom: -12px;
                    left: 50%;
                    transform: translateX(-50%);
                    border-radius: 2px;
                    box-shadow: 1px 4px 4px lighten(@dark-font-color, 8%);
                }
            }

            .service-detail {
                height: 65px;
                width: 100%;
                margin-top: 15px;

                p {
                    .resetmp;
                    font-size: 16px;
                }

                @media only screen and (orientation: landscape) {
                    height: 90px;
                }

                @media (min-width: 992px) {
                    height: 115px;
                }

                @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
                    height: 130px;
                }
            }

            .service-readmore {
                .resetmp;
                font-family: "Barlow-Medium";
                text-transform: uppercase;
                font-size: 18px;
                margin-top: 20px;
                cursor: pointer;
            }

            &:hover {
                .bg-gradient;

                .img-wrap {
                    background: @secondary-color  !important;

                    img {
                        filter: invert(99%) sepia(0%) saturate(7500%) hue-rotate(177deg) brightness(101%) contrast(103%);
                    }
                }

                h3::after {
                    background: @secondary-color;
                }
            }

            .simplebar-scrollbar {
                &::before {
                    background: @secondary-color;
                }
            }
        }
    }
}