a,
button {
    outline: none !important;
    text-decoration: none !important;
}

p {
    font-family: "Barlow-Regular";
    letter-spacing: .6px;
    // font-size: 14px;
    // line-height: 27px;
    // font-size: calc([min] + ([max] - [min]) * ((100vw - [min vw]) / ([max vw] - [min vw])));
    font-size: calc(14px + (18 - 14) * (100vw - 380px) / (1440 - 380));
    line-height: calc(20px + (27 - 20) * (100vw - 380px) / (1440 - 380));
}

h1 {
    .resetmp;
    font-family: "Barlow-BoldItalic";
    color: @light-font-color;
    letter-spacing: .5px;
    -webkit-text-stroke: 2px @primary-color-solid;
    text-transform: capitalize;
    // .font-size(40px);
    font-size: calc(30px + (40 - 30) * (100vw - 380px) / (1440 - 380));
}

h3 {
    font-family: "Barlow-MediumItalic";
    color: @light-font-color;
    letter-spacing: .5px;
    text-transform: capitalize;
    font-size: 24px;
    text-shadow: 1px 1px 2px @dark-font-color;
}

h4 {
    .resetmp;
    font-family: "Barlow-Italic";
    letter-spacing: .5px;
    text-transform: capitalize;
    // .font-size(24px);
    font-size: calc(18px + (24 - 18) * (100vw - 380px) / (1440 - 380));
}

h5 {
    .resetmp;
    font-family: "Barlow-SemiBold";
    letter-spacing: .5px;
    text-transform: uppercase;
    color: @dark-font-color;
}