section.gallery {
    .g-pd-y;
    padding-bottom: 10px;

    .teamSlider-wrap {
        padding: 50px 0 30px 0;
        background: url("../images/GalleryBG.png");
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;

        &::before {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, 0.9);
        }

        .owlTwo {
            .item {
                .img-wrap {
                    display: block;
                    position: relative;
                    margin-bottom: 20px;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }

                    &::after {
                        content: url("../images/move-object.png");
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%) scale(0);
                        transition: transform .4s, opacity .3s;
                        filter: drop-shadow(2px 2px 5px @primary-color-solid);
                        opacity: 0;

                    }

                    &::before {
                        content: "";
                        display: block;
                        width: 85%;
                        height: 90%;
                        border: 3px solid @dark-font-color;
                        background-color: rgba(white, .6);
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%) scale(0);
                        transition: transform .4s, opacity .3s;
                        opacity: 0;
                    }

                    &:hover {
                        &:before {
                            transform: translate(-50%, -50%) scale(1);
                            opacity: 1;
                        }

                        &::after {
                            transform: translate(-50%, -50%) scale(.8);
                            opacity: 1;
                        }
                    }
                }
            }
        }

        .gallery-btn-left {
            width: 62px;
            height: 62px;
            background-color: #fff;
            border: 2px solid @primary-color-solid;
            border-radius: 50%;
            cursor: pointer;
            position: absolute;
            top: 50%;
            left: 3%;
            z-index: 111;
            transform: translateY(-50%);

            &::after {
                content: url("../images/gallerySliderArrow.png");
                display: block;
                position: absolute;
                top: 46%;
                left: 50%;
                transform: translate(-50%, -50%) rotate(180deg) scale(.9);
                filter: drop-shadow(-2px -2px 1px black);
            }
        }

        .gallery-btn-right {
            width: 62px;
            height: 62px;
            background-color: #fff;
            border: 2px solid @primary-color-solid;
            border-radius: 50%;
            cursor: pointer;
            position: absolute;
            top: 50%;
            right: 3%;
            z-index: 111;
            transform: translateY(-50%);

            &::after {
                content: url("../images/gallerySliderArrow.png");
                display: block;
                position: absolute;
                top: 53%;
                left: 50%;
                transform: translate(-50%, -50%) scale(.9);
                filter: drop-shadow(2px 2px 1px black);
            }
        }
    }
}