header.topNav>nav {
    text-transform: uppercase;
    background-color: #ffffff !important;
    padding-bottom: 20px;

    @media (min-width: 992px) {
        padding-bottom: 8px;
    }

    .navbar-nav {
        .nav-item {
            margin: 0 15px;
            padding: 5px 0;

            @media (min-width: 992px) {
                padding: 20px 0;
            }

            .nav-link {
                width: 100%;
                padding: 8px 15px;
                font-family: "Barlow-SemiBold";
                font-size: 18px;
                color: @dark-font-color;
                transition: color .4s;
                border: 2px solid transparent;
                -webkit-box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0);
                -moz-box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0);
                box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0);
            }

            @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
                .nav-link {
                    font-size: 16px;
                    padding: 8px 8px;
                }
            }

            &:hover {
                .nav-link {
                    color: @primary-color-solid  !important;
                }
            }
        }

        .nav-link.active {
            background-color: #f3f3f3;
            color: @primary-color-solid  !important;
            border: 2px solid @primary-color-solid;
            -webkit-box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0.5);
            -moz-box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0.5);
            box-shadow: 4px 4px 12px -1px rgba(0, 0, 0, 0.5);
        }

        margin-bottom: 15px;

        @media (min-width: 992px) {
            margin-bottom: 0;
        }
    }

    .navButton {
        .myButton;
        padding: 10px 20px;
        -webkit-box-shadow: 3px 3px 7px -1px rgba(@primary-color-solid, 0.85);
        -moz-box-shadow: 3px 3px 7px -1px rgba(@primary-color-solid, 0.85);
        box-shadow: 3px 3px 7px -1px rgba(@primary-color-solid, 0.85);
        transition: all .5s;

        &:hover {
            box-shadow: -2px -2px 2px -1px rgba(@primary-color-solid, 0.6);
            border: 2px solid lightgrey;
        }
    }
}