section.blog {
    .g-pd-y;
    overflow: hidden;

    .video-wrap {
        border: 3px solid @primary-color-solid;
        width: 100%;
        min-height: 355px;
        position: relative;

        span {

            img {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
                cursor: pointer;
                pointer-events: none;
                z-index: 22;
            }

            &::before {
                content: url("../images/BlogPlay.png");
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0.8);
                z-index: 33;
                cursor: pointer;
                pointer-events: none;
            }
        }

        iframe {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
        }
    }

    .card-wrap {
        display: block;
        position: relative;
        margin-top: 20px;
        margin-bottom: 35px;

        .img-wrap {
            position: relative;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            &::after {
                content: "";
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: @primary-color-solid;
                opacity: 0;
                transition: opacity .2s;
            }
        }

        .title-bar {
            width: 90%;
            background-color: #f3f3f3;
            border: 2px solid @primary-color-solid;
            text-align: center;
            padding: 12px 0 8px 0;
            position: absolute;
            left: 50%;
            bottom: -30px;
            transform: translateX(-50%);
            border-radius: 2px;
            color: @dark-font-color;
            transition: color .5s;

            h5 {
                .resetmp;
                font-size: 18px;
            }

            p {
                .resetmp;
                font-size: 13px;
            }

            @media only screen and (min-device-width : 768px) and (max-device-width : 1024px) and (orientation : landscape) {
                h5 {
                    font-size: 14px;
                }

                p {
                    font-size: 9px
                }
            }
        }

        &:hover {
            .img-wrap {
                &::after {
                    opacity: .3;
                }
            }

            .title-bar {
                .bg-gradient;
                border-radius: 1px;

                h5,
                p {
                    color: @light-font-color  !important;
                }

            }
        }
    }

    .loadMore {
        .myButton;
        padding: 20px 50px;
    }
}